.filter-container {
    display: grid;
    flex-wrap: wrap;
  }
  
  .box--container {
    min-width: 300px;
    margin-top: 2%;
    margin-bottom: 2%;
  }
  
  /* .filter-container {
    font-size: 17px;
    color: #101010;
    margin-left: 0;
    font-weight: 600;
  } */
  
  @media (min-width: 768px) {
    .filter-container {
      display: flex;
      flex-direction: row;
      margin-left: 5%;
      margin-right: 5%;
      justify-content: space-evenly;
      gap: 20px;
    }
  }
  
  @media (max-width: 400px) {
    .filter-container {
      display: flex;
      flex-direction: column;     
      margin: 0;
      justify-content: space-evenly;
      column-gap: 20px;
      row-gap: 20px;
    }
  }
  