nav {
  max-width: 100% !important;
  height: 124px;
  display: flex;
  flex-direction: column;
  background: linear-gradient(rgba(8, 0, 58, 0.7), rgba(8, 0, 58, 0.7)),
    url('https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
}

.navbar-container {
  background: linear-gradient(rgba(8, 0, 58, 0.7), rgba(8, 0, 58, 0.7)),
    url('https://images.pexels.com/photos/8386440/pexels-photo-8386440.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* General styles for larger screens */
.navbar-text h1 {
  font-size: 60px; /* Default for large screens */
  color: #fff !important;
  max-width: 75%;
  word-wrap: break-word;
  font-family: 'Open Sans', sans-serif;
  padding: 0 0 10px;
  margin: 15px auto;
}

/* Smaller screens (max-width: 980px) */
@media (max-width: 980px) {
  .navbar-text h1 {
    font-size: 40px; /* Adjust size for smaller screens */
    color: #fff !important;
  }
}

/* Even smaller screens (max-width: 768px) */
@media (max-width: 768px) {
  .navbar-text h1 {
    font-size: 20px; /* Further adjustment */
    color: #fff !important;
    padding: 60px 0px 50px;
  }
}
